
  import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

  import { mapActions } from 'vuex'
  
  export default {
    components: {
      Group,
      XInput,
      XButton
    },
    data () {
      return {
        model: {
          account: '',
          pwd: '',
          code: ''
        }
      }
    },
    methods: {
      ...mapActions([
      	'GetLogin'
      ]),
      checkLoginInfo() {
        if(this.model.account==="" || this.model.pwd === "") {
	  	  this.$vux.alert.show('账户密码不能为空！')
	  	  return false
	  	}else {
	  		return true
	  	}
      },
      submitLogin () {
      	const flag = this.checkLoginInfo()
      	if(flag) {
      		setTimeout(res => (
	      		this.GetLogin(this.model).then((res) => {
	      		    if(res.status.code === 0) {
					  	this.$vux.alert.show(res.status.desc)
					}else {
					    this.$router.push('/')
					}
	      		})
      		), 500)
      	}
      }
    },
    created () {
      // this.$store.commit('isLoading', false)
    }
  }



